// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import Header from './components/head'
import Mainly from './components/main.vue'
import Archives from './components/archives.vue'
import LeaveComments from './components/leave_comments.vue'
import Link from './components/link.vue'
import About from './components/about.vue'
import AsideBy from './components/aside.vue'
import Foot from './components/foot.vue'
import Login from './components/login.vue'
import Page from './components/page.vue'


// 设置反向代理，前端请求默认发送到 http://localhost:84/api
//8443是tomcat打开SSL的文本服务的默认端口，默认配置文件里用的端口就是8443。默认的https端口号是443，因此tomcat为了区别使用了8443这个端口
var axios = require('axios')
axios.defaults.baseURL = '/api'
// 全局注册，之后可在其他组件中通过 this.$axios 发送数据
Vue.prototype.$axios = axios


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { Header,Mainly,Archives,LeaveComments,Link,About,AsideBy,Foot,Login,Page},
  template: `
  <div>
    <Header/>
    <div class="container">   
      <keep-alive>
        <router-view></router-view>   
      </keep-alive>       
      <AsideBy/>
    </div>
    <Foot/>
  </div>`
})



