<template>
    <div class="modal fade" id="loginModal" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" aria-hidden="true" >
        <div style="display:table; width:100%; height:100%;">
            <div style="vertical-align:middle; display:table-cell;">
                <div class="modal-dialog modal-sm" style="width:540px;">
                    <div class="modal-content" style="border: none;">
                        <div class="col-left"></div>
                        <div class="col-right">
                            <div class="modal-header">
                                <button type="button" id="login_close" class="close" data-dismiss="modal"><span aria-hidden="true">×</span><span class="sr-only">Close</span></button>
                                <h4 class="modal-title" id="loginModalLabel" style="font-size: 18px;">登录</h4>
                            </div>
                            <div class="modal-body">
                                <section class="box-login v5-input-txt" id="box-login">
                                    <form id="login_form" action="" method="post" autocomplete="off">
                                        
                                        <ul>
                                            <li class="form-group"><input class="form-control" id="id_account_l" maxlength="50" name="account_l" placeholder="请输入邮箱账号/手机号" type="text"></li>
                                            <li class="form-group"><input class="form-control" id="id_password_l" name="password_l" placeholder="请输入密码" type="password"></li>                                 
                                        </ul>
                                    </form>
                                    <p class="good-tips marginB10">
                                        <a id="btnForgetpsw" class="fr">忘记密码？</a>还没有账号？<a href="javascript:;" target="_blank" id="btnRegister">立即注册</a>
                                    </p>
                                    <div class="login-box marginB10">
                                        <button id="login_btn" type="button" class="btn btn-micv5 btn-block globalLogin">登录</button>
                                        <div id="login-form-tips" class="tips-error bg-danger" style="display: none;">错误提示</div>
                                    </div>                       
                                    <div class="threeLogin">
                                        <span>其他方式登录</span>
                                        <a class="nqq" href="javascript:;"></a>
                                        <a class="nwx" href="javascript:;"></a><!--<a class="nwb"></a>-->
                                    </div>                       
                                </section>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import bootstrap from '../../static/login/js/bootstrap.min.js';
import common from '../../static/login/js/common.js';
import jquery2 from '../../static/login/js/jquery2.2.2.min.js';
//import login from '../../static/login/js/login.js';
$("#loginModal").modal("show");
export default {
    name:'login',
    components:{}
    
}
</script>
<style scoped>
    
    @import '../../static/login/css/index.css';
    @import '../../static/login/css/bootstrap.min.css'
</style>



