<template>
    <!-- <div>
        <h1>这里是主页！</h1>   
    </div> -->
    <!--最新文章-->
    <section class="mysection">
        <!--  轮播图开始-->
        <div id="ups-banner">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <div class="content">
                            <div class="txt">
                                <h3>轮播图1标题</h3>
                                <p>轮播图1子标题</p>
                            </div>
                            <a href='' class="link">详情点击</a>
                        </div>
                        <div class="img-fix">
                            <div class="img-inner">
                                <img src="" alt="轮播图1图片" class="imgs">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="bottom-nav">
                    <div class="ups-icon-videoplay"></div>
                    <div class="swiper-pagination"></div>
                </div>
                <div class="swiper-button-next"></div>
                <div class="swiper-button-prev"></div>
            </div>
        </div>
        <!--  轮播图结束-->

        <div class="arclist">
            <h4 class="index-title homeh4">
                <a href="\">
                    <i class="el-certificate"></i>最新文章
                    <small>New Article</small>
                </a>
            </h4>
            <!--列表开始-->
            <ul>
                <li  v-for='blog in blogs' :key="blog.blogId" :class="isSupport(blog.support)">
                    <div class="arcimg">
                        <img :src="blog.headerImg" alt="博客图片" :title="blog.title"/>
                    </div>
                    <div class="arc-right">
                        <h4 class="blue-text">
                            <router-link :to="{name:'Article',params:{blogId:blog.blogId}}" 
                            :title="blog.title">{{blog.title}}</router-link>                            
                        </h4>
                        <p>{{blog.summary}}</p>
                        <ul>
                            <li>
                                <!-- a标签title提示的设置在showtip.js -->
                                <a href="" :title="'卑微小袁' + blog.createTime + '发表'" >
                                    <i class="el-time"></i>
                                    <span>时间：{{blog.createTime}}</span>
                                </a>
                            </li>
                            <li>
                                <a href="" title="作者:卑微小袁">
                                    <i class="el-user"></i>卑微小袁
                                </a>
                            </li>
                            <li>
                                <a href="">
                                    <i class="el-comment"></i>
                                    <span class="cy_cmt_count"></span>
                                </a>
                            </li>
                            <li><a :title="'已有' + blog.click + '次浏览'">
                                    <i class="el-eye-open"></i>
                                    <span>已有{{blog.click}}次浏览</span>
                                </a>
                            </li>
                            <li>
                                <a title="查看分类">
                                    <i class="el-th-list"></i>
                                    <span>{{blog.categoryTitle}}</span>
                                </a>
                            </li>
                        </ul>
                    </div>
                </li>
            </ul>
        </div>
        <!--分页开始-->
        
        <page :blogPageInfo="pageInfo" @someOfPageBlogs="loadBlogs"></page>
        <!--分页结束 <page></page>-->
    </section>
</template>

<script>
//分页组件
import page from './page';
export default {
    /*数据：{blogId:1,title:'java',headerImg:'',summary:'web技术',click:10,createTime:'2019-07-02',categoryTitle:'vue技术',support:1},
            {blogId:1,title:'c',headerImg:'',summary:'web技术',click:12,createTime:'2019-07-02',categoryTitle:'vue技术',support:1},
            {blogId:1,title:'c++',headerImg:'',summary:'web技术',click:13,createTime:'2019-07-02',categoryTitle:'vue技术',support:1},
            {blogId:1,title:'c#',headerImg:'',summary:'web技术',click:14,createTime:'2019-07-02',categoryTitle:'vue技术',support:1},
            {blogId:1,title:'php',headerImg:'',summary:'web技术',click:15,createTime:'2019-07-02',categoryTitle:'vue技术',support:0}*/
    name: 'mainly',
    components: {page},
    data () {
        return {
            blogs: [],

            //分页对象,从后台一并获取
            /*total:8,pageNum:1,pageSize:3,size:3,pages:3,prePage:null,nextPage:2,isFirstPage:true,
                    isLastPage:false,hasPreviousPage:false,hasNextPage:true,navigatePages:3,navigatepageNums:[1,2,3],navigateFirstPage:1,navigateLastPage:3*/
            pageInfo:[]
            
            
        }
    },
    mounted:function() {        
        //生命周期：在html渲染后执行
        this.loadBlogs();
    },
    methods: {
        //加载用于显示博客文章数据信息,换页时加载新的博客
        loadBlogs (url) {
            var _this = this; 
            if(!url) url=""; 
                      
            this.$axios.get('/mainPage/body?'+url).then(resp => {
                if (resp && resp.status === 200) {                    
                    _this.blogs = resp.data.list;//暂时后台传pageInfo，里面的list是blog对象
                    //_this.blogPageInfo = resp.data.list;
                    _this.pageInfo = resp.data;
                }	
            });

        },
        
        //判断是否为推荐的博文
        isSupport (support) {
            if(support == 1)
                return 'tuijian';
            else
                return '';
        }
    }
}
</script>

<style scoped>
    .swiper-container {
            width: 100%;
            max-width: 1600px;
            margin: 0 auto;
        }

    .swiper-slide {
        position: relative;
    }

    .swiper-slide .content {
        width: 50%;
        max-width: 460px;
        position: absolute;
        right: 60px;
        bottom: 90px;
        z-index: 20;
    }

    @media screen and (max-width: 768px) {
        .swiper-slide .content {
            display: none;
        }
    }

    .swiper-slide .content .txt {
        padding: 20px;
        background: rgba(255, 255, 255, 0.85);
    }

    .swiper-slide .content .txt h3 {
        font-family: "BerlingskeSans Serif", Georgia, Times, serif;
        margin: 0;
        font-size: 2.3em;
        font-weight: normal;
    }

    .swiper-slide .content .txt p {
        margin: 5px 0 0;
        font-size: 1.2em;
    }

    .swiper-slide .content .link {
        border-radius: 25px;
        display: block;
        background: #008369;
        transition: .2s background;
        color: #fff;
        font-family: "BerlingskeSans Bd", Tahoma, helvetica, arial, sans-serif;
        text-align: center;
        font-weight: 400;
        font-size: 1.1em;
        line-height: 1.1em;
        padding: 10px 30px 10px;
        float: right;
        margin-top: 15px;
        text-decoration: none;
    }

    .swiper-slide .content .link:hover {
        background: #006a55;
    }

    .img-fix {
        margin-top: -25%;
        overflow: hidden;
    }

    .img-inner {
        transform: translateY(0%);
    }

    .swiper-container img {
        width: 100%;
        display: block;
    }

    .swiper-button-next, .swiper-button-prev {
        background: none;
        top: auto;
        bottom: 30px;
    }

    .swiper-button-prev {
        left: 0;
    }

    .swiper-button-next {
        right: 40px;
    }

    .swiper-button-next:before, .swiper-button-prev:before {
        font-family: "upsicons";
        position: absolute;
        display: block;
        height: 54px;
        width: 50px;
        top: 0;
        left: 0;
        text-align: center;
        line-height: 54px;
        color: #fff;
        font-size: 68px;
    }

    .swiper-button-prev:before {
        content: '\e628';
    }

    .swiper-button-next:before {
        content: '\e62a';
    }

    .bottom-nav {
        width: 100%;
        text-align: center;
        position: absolute;
        bottom: 30px;
        z-index: 10;
    }

    .ups-icon-videoplay {
        display: inline-block;
        vertical-align: middle;
        margin-right: 15px;
        cursor: pointer;
    }

    .ups-icon-videoplay:before {
        font-family: "upsicons";
        content: "\e680";
        font-size: 28px;
        color: white;
    }

    .stop-status.ups-icon-videoplay:before {
        content: "\e681";
    }

    .swiper-pagination {
        position: relative;
        display: inline-block;
        height: 29px;
        vertical-align: middle;
    }

    #ups-banner .swiper-pagination-bullet {
        width: 18px;
        height: 18px;
        background: #fff;
        opacity: 1;
        box-sizing: border-box;
        border: 3px solid #fff;
        margin: 0 15px;
        vertical-align: middle;
    }

    #ups-banner .swiper-pagination-bullet-active {
        background: #ffb500;
    }

    @media (max-width: 767px) {
        .swiper-button-prev:before {
            content: '\e627';
        }

        .swiper-button-next:before {
            content: '\e629';
        }

        .swiper-button-next, .swiper-button-prev {
            bottom: 10px;
        }

        .swiper-button-next:before, .swiper-button-prev:before {
            font-size: 32px;
        }

        .bottom-nav {
            bottom: 10px;
        }

        #ups-banner .swiper-pagination-bullet {
            width: 12px;
            height: 12px;
            border-width: 2px;
            margin: 0 10px;
        }

        .ups-icon-videoplay {
            margin-right: 10px;
        }
    }
</style>



